<template>
  <div class="homeContent">
    <news></news>
    <div class="homeContent-center">
        <mainLeft></mainLeft>
        <mainRight></mainRight>
    </div>
  </div>
</template>

<script>
import news from "~/components/news.vue";
import mainLeft from "~/components/mainLeft.vue";
import mainRight from "~/components/mainRight.vue";

export default {
  data() {
    return {};
  },
  components: {
    news,
    mainLeft,
    mainRight
  }
};
</script>

<style scoped lang="scss">
main::after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}
.mainLeft {
  width: 300px;
  margin-right: 20px;
  margin-bottom: 30px;
  float: left;
}

.mainRight {
  overflow: hidden;
}
</style>



